.wrapper {
  padding: 0 3.2rem 0 15rem;
}

.contacts-list, .footer-info, .advantages-inner, .our-glass, .company, .menu-list, .navigation, .header-info, .header-title {
  display: flex;
}

.advantages-text-inner, .price-list, .glass-text__inner {
  display: grid;
}

.price__number {
  display: inline-block;
}

.price__text_wrap {
  display: block;
}

.advantages__subtitle, .price__text, .price__list, .header__info-list {
  font-family: "Gilroy", sans-serif;
}

.text__number, .block-title {
  font-family: "FuturaPT", sans-serif;
}

.advantages-image, .advantages__image, .price__number, .price__title, .cost__inner, .glass__text, .glass__image, .glass-image, .company__image, .company-image__small, .company-text__first, .navigation, .header-info, .header-title, .header, .wrapper-side-block {
  position: relative;
}

.price__number::after, .cost-side-text, .company-text__first::before, .text__number, .price__title::before, .advantages-image::before, .glass-image::before, .company-image__small::before {
  position: absolute;
}

.advantages__subtitle, .price__number, .price__text, .price__title {
  text-transform: uppercase;
}

.advantages-image::before, .glass-image::before, .company-image__small::before {
  content: "";
  width: 6rem;
  height: 6rem;
  border: 2px solid #51d1c5;
}

.price__title::before {
  content: "";
  width: 4.5rem;
  height: 2px;
  background-color: #ffffff;
}

@font-face {
  font-family: "Futura";
  src: local("FuturaLightC"), url(../../assets/fonts/futura/FuturaLightC.woff) format("woff"), url(../../assets/fonts/futura/FuturaLightC.ttf) format("truetype");
  font-weight: 300;
}
@font-face {
  font-family: "Futura";
  src: local("FuturaMediumC"), url(../../assets/fonts/futura/FuturaMediumC.woff) format("woff"), url(../../assets/fonts/futura/FuturaMediumC.ttf) format("truetype");
  font-weight: 500;
}
@font-face {
  font-family: "FuturaPT";
  src: local("FuturaPT Bold"), url(../../assets/fonts/futuraPt/FuturaPT-Bold.woff) format("woff"), url(../../assets/fonts/futuraPt/FuturaPT-Bold.ttf) format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: "Gilroy";
  src: local("Gilroy Light"), url(../../assets/fonts/gilroy/Gilroy-Light.woff) format("woff"), url(../../assets/fonts/gilroy/Gilroy-Light.ttf) format("truetype");
  font-weight: 300;
}
*,
::after,
::before {
  box-sizing: border-box;
}

:root {
  font-size: 10px;
}

body {
  font-family: "Futura", sans-serif;
  margin: 0;
  padding: 0;
  color: #1b1b1b;
  font-size: 1.8rem;
  line-height: 2.1rem;
  font-weight: 300;
  font-style: normal;
  scroll-behavior: smooth;
}

button {
  outline: none;
  border: none;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

ul {
  list-style: none;
}

li {
  text-decoration: none;
}

input {
  border: none;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

.wrapper {
  max-width: 146.2rem;
  margin: 0 auto;
  border-right: 2px solid #323232;
  border-left: 2px solid #323232;
}

.side-block {
  width: 7rem;
  position: absolute;
  height: 100%;
  top: 0;
  right: -14rem;
  background: linear-gradient(#72afd3, #37ecba);
  z-index: 50;
}

.main-title {
  max-width: 60rem;
  font-size: 6rem;
  line-height: 6rem;
  font-weight: 300;
  font-style: normal;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: -1.7px;
}

.block-title {
  font-size: 2.4rem;
  line-height: 2.4rem;
  font-weight: 700;
  font-style: normal;
  text-transform: uppercase;
}

.text__number {
  padding: 0.5rem 0 0 0;
  font-size: 2.4rem;
  line-height: 2.4rem;
  font-weight: 700;
  font-style: normal;
}

.header {
  background-color: #1b1b1b;
}

.header-nav {
  margin: 0 3.4rem 0 0;
  align-self: center;
}

.header-padding {
  padding: 3.3rem 0 0 0;
}

.header-title {
  z-index: 20;
  justify-content: flex-end;
  padding: 35.4rem 3rem 13.2rem 0;
}

.header-info {
  justify-content: flex-end;
  padding: 0 19.6rem 10rem 0;
  z-index: 20;
}

.header__info-list {
  max-width: 55rem;
  padding: 3rem 2.2rem 10.8rem 24rem;
  border: 1px solid #ffffff;
}

.info__list {
  text-transform: uppercase;
  color: #ffffff;
  font-size: 1.4rem;
  line-height: 1.4rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.8px;
}

.info-margin {
  margin: 1.2rem 0;
}

.navigation {
  justify-content: space-between;
  z-index: 20;
}

.header-bg-image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -4;
}

.navigation__margin {
  margin: 0 2.4rem 0 2.9rem;
}

.menu__link {
  color: #ffffff;
  border-bottom: 2px solid transparent;
  padding-bottom: 5px;
  font-size: 1.6rem;
  line-height: 1.6rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.8px;
  transition: all 0.6s ease;
}

.menu__link:hover {
  color: #51d1c5;
  border-bottom: 2px solid #51d1c5;
}

.company {
  padding: 8rem 0 0 0;
}

.company__title {
  padding: 0 0 3.8rem 0;
}

.company-text {
  max-width: 63.2rem;
}

.company__text {
  font-size: 1.8rem;
  line-height: 2.1rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 1px;
}

.company-text__first {
  padding: 0 7rem 2rem 10.4rem;
}

.company-text__first::before {
  content: "";
  top: 8%;
  left: 0;
  height: 2px;
  width: 8.6rem;
  background-color: #1b1b1b;
}

.company-text__second {
  padding: 0 7rem 0 10.4rem;
}

.company-image {
  align-self: flex-end;
  margin: 0 0 1.4rem 0;
}

.company-image__small {
  padding: 0 2.6rem 0 0;
}

.company-image__small::before {
  top: -13%;
  left: -15%;
  z-index: 1;
}

.company__image {
  z-index: 20;
}

.our-glass {
  padding: 15.6rem 0 8.6rem 0;
}

.glass__title {
  padding: 0 0 3.8rem 0;
}

.glass__image {
  z-index: 20;
}

.glass-image::before {
  bottom: -7%;
  right: -5%;
  z-index: 1;
}

.glass-text {
  padding: 0 0 0 10rem;
}

.glass__text {
  padding: 0 0 0 4rem;
  letter-spacing: 0.8px;
}

.glass__number {
  left: 0;
}

.glass-text__inner {
  grid-template-columns: 27.2rem 25rem;
  gap: 5.4rem 4.8rem;
}

.glass__sub-text {
  display: inline-block;
}

.cost {
  background-color: #1b1b1b;
}

.cost__inner {
  padding: 8rem 0 4rem 20.8rem;
}

.cost-side-text {
  transform: rotate(-90deg);
  top: 44%;
  left: -12.5%;
  font-size: 8.4rem;
  line-height: 8.4rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.6px;
  color: #ffffff;
}

.price-list {
  grid-template-columns: 35rem 35rem;
  gap: 11.8rem 7rem;
  padding: 0 1rem 0 0;
  color: #ffffff;
}

.price__title::before {
  top: 50%;
  left: -6.6rem;
}

.price__list {
  padding: 0 0 4 4rem;
}

.price__text {
  padding: 1rem 0 1.6rem 0;
  font-size: 1.4rem;
  line-height: 1.8rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.6px;
}

.price__text-number {
  font-size: 3.4rem;
  line-height: 3.4rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 2px;
}

.price__text-number {
  color: #51d1c5;
}

.price__number {
  padding: 0 0.4rem 0 0;
}

.price__number::after {
  content: "2";
  color: #51d1c5;
  font-size: 1.8rem;
  line-height: 1.8rem;
  font-weight: 300;
  font-style: normal;
  top: 2px;
  right: -5px;
}

.cost-undercut {
  border-top: 2px solid #323232;
  padding: 0 0 8rem 0;
}

.cost-undercut__text {
  padding: 4.2rem 8.8rem 0 0;
  font-size: 1.4rem;
  line-height: 1.8rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.6px;
  color: #8d8d8d;
}

.cost__wrapper {
  padding: 0 0 0 15rem;
}

.advantages {
  background-color: #ffffff;
}

.advantages-inner {
  padding: 8.4rem 0 7.3rem 0;
}

.advantages__title {
  max-width: 41rem;
}

.advantages__subtitle {
  max-width: 85rem;
  padding: 4.4rem 0 1.2rem 0;
  font-size: 1.8rem;
  line-height: 2.2rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.8px;
}

.advantages-text-inner {
  grid-template-columns: 45rem 42rem;
  gap: 3.4rem 1.6rem;
  padding: 0 5.8rem 0 0;
}

.advantages__image {
  z-index: 20;
}

.advantages-image::before {
  bottom: -18px;
  left: -30px;
  z-index: 1;
}

.footer {
  background-color: #1b1b1b;
}

.footer-info {
  padding: 6.8rem 0 0 0;
  justify-content: space-between;
}

.align-self-center {
  align-self: center;
}

.contact__link {
  font-size: 1.6rem;
  line-height: 1.6rem;
  font-weight: 500;
  font-style: normal;
  letter-spacing: 0.8px;
  color: #ffffff;
  text-decoration: none;
}

.contact-margin {
  margin: 0 5.7rem;
}

.copyright {
  padding: 7.4rem 0 5.8rem 0;
}

.copyright-text {
  font-size: 1.6rem;
  line-height: 1.6rem;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.8px;
  color: #a1a1a1;
}